<template>
    <div style="padding-top: 2px;">
        <el-form :inline="true" ref="searchFormRef" :model="searchFormData" size="mini">
            <el-form-item label="路由显示名称" prop="router_show_name">
                <el-input v-model="searchFormData.router_show_name" placeholder="请输入路由显示名称"></el-input>
            </el-form-item>
            <el-form-item label="路由名称" prop="router_name">
                <el-input v-model="searchFormData.router_name" placeholder="请输入路由名称"></el-input>
            </el-form-item>
            <el-form-item label="创建时间" prop="createTime">
                <el-date-picker
                        v-model="searchFormData.createTime"
                        type="daterange"
                        range-separator="至"
                        start-placeholder="开始日期"
                        end-placeholder="结束日期">
                </el-date-picker>
            </el-form-item>
            <div class="v-search-opt">
                <div class="v-pagination">
                    <el-pagination
                            @size-change="handleSizeChange"
                            @current-change="handleCurrentChange"
                            :current-page="searchFormData.pageNum"
                            :page-size="searchFormData.pageSize"
                            :total="searchFormData.pageCount"
                            :page-sizes="[14, 20, 50, 100]"
                            layout="total, sizes, prev, pager, next, jumper"
                    >
                    </el-pagination>
                </div>
                <div class="v-inline">
                    <el-button size="mini" type="primary" @click="onSearch"><i class="iconfont icon-chaxun"></i> 查询</el-button>
                    <el-button size="mini" type="primary" @click="onReset"><i class="iconfont icon-chongzhi"></i> 重置</el-button>
                    <el-button size="mini" type="primary" @click="onAdd(module_name)"><i class="iconfont icon-xinzeng"></i> 新增</el-button>
                </div>
            </div>
        </el-form>
        <hr>
        <el-table :data="tableData" :border="true" :stripe="true" size="mini" :height="getTableHeight(130)">
            <el-table-column fixed type="index" :index="onIndexMethod(searchFormData.pageNum,searchFormData.pageSize)" label="序号" width="60"></el-table-column>
            <el-table-column
                    prop="createTime"
                    label="创建时间"
                    width="156">
            </el-table-column>
            <el-table-column
                    :show-overflow-tooltip="true"
                    prop="router_show_name"
                    label="显示名称"
                    width="110">
            </el-table-column>
            <el-table-column
                    :show-overflow-tooltip="true"
                    prop="router_name"
                    label="路由名称"
                    width="110">
            </el-table-column>
            <el-table-column
                    prop="router_has_icon"
                    :formatter="$format_bool"
                    label="附带图标"
                    width="80">
            </el-table-column>
            <el-table-column
                    label="图标显示"
                    width="80">
                <template slot-scope="scope">
                    <i :class="'iconfont ' + scope.row.router_icon_class "></i>
                </template>
            </el-table-column>
            <el-table-column
                    :show-overflow-tooltip="true"
                    prop="router_icon_class"
                    label="图标样式"
                    width="180">
            </el-table-column>
            <el-table-column
                    prop="router_icon_size"
                    width="80"
                    label="图标尺寸">
            </el-table-column>
            <el-table-column
                    :show-overflow-tooltip="true"
                    prop="router_path"
                    label="路由路径">
            </el-table-column>
            <el-table-column fixed="right" label="操作" width="160">
                <template slot-scope="scope">
                    <el-col :span="8">
                        <el-tooltip class="item" effect="dark" content="查看" placement="left">
                            <el-button @click.native.prevent="onView(module_name,scope.$index, tableData)" type="text" size="small"><i class="iconfont icon-chakan"></i></el-button>
                        </el-tooltip>
                    </el-col>
                    <el-col :span="8">
                        <el-tooltip class="item" effect="dark" content="修改" placement="left">
                            <el-button @click.native.prevent="onEdit(module_name,scope.$index, tableData)" type="text" size="small"><i class="iconfont icon-xiugai"></i></el-button>
                        </el-tooltip>
                    </el-col>
                    <el-col :span="8">
                        <el-tooltip class="item" effect="dark" content="删除" placement="left">
                            <el-button @click.native.prevent="onDelete(scope.$index, tableData)" type="text" size="small"><i class="iconfont icon-shanchu icon-red"></i></el-button>
                        </el-tooltip>
                    </el-col>
                </template>
            </el-table-column>

        </el-table>
    </div>
</template>

<script>
    export default {
        name: 'sysRouterInfoManageList',
        data() {
            return {
                module_name: 'sysRouterInfoManage',
                refresh_page_method: 'refresh_' + this.module_name,
                project_type_code_list: null,
                project_env_code_list: null,
                project_state_code_list: null,
                project_team_code_list: null,
                searchFormData: {
                    pageNum: 1,
                    pageSize: 14,
                    pageCount: 1,
                    order: '+router_path'
                },
                tableData: []
            }
        },
        created() {
            this.$store.state.Apps.$on(this.refresh_page_method, () => {
                this.search();
            });
        },
        async mounted() {
            this.search();
        },
        methods: {
            handleSizeChange(val) //修改单页显示条数
            {
                this.searchFormData.pageSize = val;
                this.search();
            },
            handleCurrentChange(val) //当前页修改
            {
                this.searchFormData.pageNum = val;
                this.search();
            },
            onReset() // 重置功能
            {
                this.$refs.searchFormRef.resetFields();
                this.search();
            },
            onSearch() // 查询按钮
            {
                this.searchFormData.pageNum = 1;
                this.search();
            },
            async search() {
                let data = await this.$search(this.searchFormData, 'OAMP_search_sys_router_info_list');
                this.searchFormData.pageCount = data.pageCount;
                this.tableData = data.data
            },
            async onDelete(index, rows)  // 删除
            {
                let rowData = rows[index];
                if (this.$del(rowData, 'OAMP_delete_sys_router_info')) {
                    rows.splice(index, 1);
                    this.$message.success('删除数据成功');
                    this.search();
                }
                else {
                    this.$message.error('删除数据失败');
                }
            },
        }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>



